.social__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
}

.social__item {
    margin-right: 15px;

    &:last-child {
        margin-right: 0;
    }
}

.social__link {
    display: flex;
    align-items: center;
    padding: 5px;
    color: $color-default-background;

    @include hover {
        color: $color-sky-blue;
    }
}